.UserList{
  &-header{
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &Left{
      display: flex;
    }
    &Form{
      display: flex;
      margin-top: 20px;
      justify-content: space-around;
      align-items: flex-end;
      &Item{
        width: 100%;
        margin: 0 20px;
      }
      &Reset{
        display: flex;
        align-items: flex-end;
        margin-bottom: 0;
        margin-left: 20px;
      }
    }
  }
  &-btn{
      display: flex;
      width: 100%;
      justify-content: flex-end;
      margin-top: 10px;
    margin-bottom: 10px;
    &Mr20{
      margin-right: 20px;
    }
  }
}
@primary-color: #1890ff;